.alerta{
    padding: .5rem;
    text-align: center;
    color: $blanco;
    font-weight: $bold;
    text-transform: uppercase;
    margin: 1rem 0;
    &.error{
        background-color: $rojo;
    }
    &.exito{
        background-color: $verde;
    }
}

table.productos{
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    thead{
        background-color: $verde;
        th{
            color: $blanco;
            padding: 2rem;
        }
    }
    td,tr{
        text-align: center;
    }
    .imagen-tabla{
        width: 10rem;
        margin: auto;
    }
}
.imagen-small{
    width: 20rem;
    display: inline-block;
    margin: 0 1rem;
}
.videos-small{
    width: 20rem;
    display: inline-block;
    margin: 0 1rem;
}